import React, { Component } from 'react';
import Button from '../../Components/UI/Button/Button';
import Input from '../../Components/UI/Input/Input';
import classes from './Auth.css';
import Spinner from './../../Components/UI/Spinner/Spinner';
import {connect} from 'react-redux';
import * as  actions from '../../Store/actions/index';
import { Redirect } from 'react-router';

class Auth extends Component{
    state = { 
        controls:{
            email: {
                elementType: 'input',
                elementConfig: {
                    type: 'email',
                    placeholder: 'Your E-Mail'
                },
                value: '',
                validation: {
                    required: true,
                    isEmail: true
                },
                valid: false,
                touched: false
            },
            password: {
                elementType: 'input',
                elementConfig: {
                    type: 'password',
                    placeholder: 'Your Password'
                },
                value: '',
                validation: {
                    required: true,
                    minLength: 7
                },
                valid: false,
                touched: false
            },
        },
        isSignUp: true
    }


    componentDidMount(){
        if(!this.props.building && this.props.authRedirectPath !== '/' )
            this.props.onSetRedirectPath();
    }
    checkValidity = (value,rules) =>{
        let isValid = true;
        if(rules.required){
            isValid = value.trim() !== '' && isValid;
        }
        if(rules.minLength){
            isValid = value.length >= rules.minLength && isValid;   
        }
        if(rules.maxLength){
            isValid = value.length <= rules.maxLength && isValid; 
        }
        if (rules.isEmail) {
            const pattern = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
            isValid = pattern.test(value) && isValid
        }

        if (rules.isNumeric) {
            const pattern = /^\d+$/;
            isValid = pattern.test(value) && isValid
        }
        return isValid;
    }
    InputHandler = (event,controlName) => {
        const UpdatedControls = {
            ...this.state.controls,
            [controlName]: {
                ...this.state.controls[controlName],
                value: event.target.value,
                valid: this.checkValidity(event.target.value,this.state.controls[controlName].validation),
                touched: true
            }
        }
        this.setState({controls: UpdatedControls})
}


    OnSubmitHandler = (event) => {
        event.preventDefault();
        this.props.onAuth(this.state.controls.email.value,this.state.controls.password.value,this.state.isSignUp);
    }
    SwitchAuthModeHandler = () =>{
        this.setState(prevState => {
            return{isSignUp: !prevState.isSignUp}
        });
    }
    render(){
        const formElementArray = [];
        for(let key in this.state.controls){
            formElementArray.push({
                id: key,
                config: this.state.controls[key]
            });
        }
        let form = formElementArray.map(formElement => (
                <Input elementType = {formElement.config.elementType}
                invalid = {!formElement.config.valid}
                shouldValidate = {formElement.config.validation}
                elementConfig = {formElement.config.elementConfig}
                touched = {formElement.config.touched}
                value = {formElement.config.value} key = {formElement.id} changed = {(event) => this.InputHandler(event,formElement.id)}/>
            ));
            if(this.props.loading)
                form = <Spinner />;
            let errorMessage = null;
            if(this.props.error){
                errorMessage = <p>{this.props.error.message}</p>
            }

            let authRedirect = null;
            if(this.props.isAuthenticated)
                authRedirect = <Redirect to = {this.props.authRedirectPath} />
        return(
            <div className = {classes.Auth}>
                {authRedirect}
                {errorMessage}
                <form onSubmit= {this.OnSubmitHandler}>
                    {form}
                    <Button btnType = 'Success' >Submit</Button>
                </form>
                <Button btnType = 'Danger' clicked = {this.SwitchAuthModeHandler}>Switch To {this.state.isSignUp ? 'SIGNIN' : 'SIGNUP'}</Button>
            </div>

        );
    }
}
const MapStateToProps = state => {
    return {
        loading: state.auth.loading,
        error: state.auth.error,
        isAuthenticated: state.auth.token !== null,
        building: state.burgerBuilder.building,
        authRedirectPath: state.auth.authRedirectPath
    }
}
const MapDispatchToProps = (dispatch) => {
    return{
        onAuth: (email,password,isSignUp) => dispatch(actions.auth(email,password,isSignUp)),
        onSetRedirectPath: () => dispatch(actions.authRedirectPath('/'))
    }
}

export default connect(MapStateToProps,MapDispatchToProps)(Auth);


